import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { GiaSTOutroCredito } from '../../../../../models/dtos/mlf/giaSTOutroCredito';
import OcorrenciaGiaRjIS from '../../../../components/mlf/ocorrenciaGiaRjIS/OcorrenciaGiaRjIS';

const GiaSTOutroCreditoEditView: FC<DetailEditViewFormProps<GiaSTOutroCredito>> = props => {
  const formProps = useForm<GiaSTOutroCredito>({ defaultValues: { ...props.selectedItem }, mode: 'onBlur' });

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <OcorrenciaGiaRjIS
            formProps={{ ...formProps, validation: { required: true } }}
            label="Ocorrência"
            placeholder="Selecione Uma Ocorrência"
            id="ocorrencia"
            isPrependLabel={true}
            inputSearchProps={{ size: 'xl' }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="valor"
            isPrependLabel={true}
            label="Valor"
            placeholder="Informe o Valor"
            type={InputType.NUMBER}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 120 } }}
            label="Dado 1"
            placeholder="Informe o Dado Complementar 1"
            id="dado1"
            isPrependLabel={true}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 120 } }}
            label="Dado 2"
            placeholder="Informe o Dado Complementar 2"
            id="dado2"
            isPrependLabel={true}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 150 } }}
            id="descricaoComplementar"
            isPrependLabel={true}
            label="Descrição Complementar"
            placeholder="Informe a Descrição Complementar"
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default GiaSTOutroCreditoEditView;
