import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType, onlyNumber } from 'summer';
import { GiaSCETransferido } from '../../../../../models/dtos/mlf/giaSCETransferido';
import FinalidadeIS from '../../../../components/ger/finalidadeIS/FinalidadeIS';

const GiaSCETransferidoEditView: FC<DetailEditViewFormProps<GiaSCETransferido>> = props => {
  const formProps = useForm<GiaSCETransferido>({ defaultValues: { ...props.selectedItem }, mode: 'onBlur' });

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <FinalidadeIS
            formProps={{ ...formProps, validation: { required: true } }}
            label="Finalidade"
            placeholder="Selecione uma finalidade"
            id="finalidade"
            isPrependLabel={true}
            inputSearchProps={{ size: 'xl' }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          {
            // TODO implementar SaidaIS aqui
          }
          <p>Colocar Input para selecionar Saída aqui -- required</p>
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="valor"
            isPrependLabel={true}
            label="Valor"
            placeholder="Informe o Valor"
            type={InputType.NUMBER}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 300 } }}
            id="dadoComplementar"
            isPrependLabel={true}
            label="Dado Complementar"
            placeholder="Informe o Dado Complementar"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <FinalidadeIS
            formProps={formProps}
            label="Finalidade Destinatário"
            placeholder="Selecione uma finalidade destinatária"
            id="finalidadeDestinatario"
            isPrependLabel={true}
            inputSearchProps={{ size: 'xl' }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 60 } }}
            label="Número inscrição destinatário"
            placeholder="Informe o Número de Inscrição do Destinatário"
            id="numeroInscricaoDestinatario"
            isPrependLabel={true}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 300 } }}
            label="Dado complementar destinatário"
            placeholder="Informe o Dado Complementar do Destinatário"
            id="dadoComplementarDestinatario"
            isPrependLabel={true}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 20 } }}
            label="Número do processo"
            placeholder="Informe o Número do Processo"
            id="numeroProcesso"
            isPrependLabel={true}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { ...onlyNumber(0, 4) } }}
            label="Ano do processo"
            placeholder="Informe o Ano do Processo"
            id="anoProcesso"
            isPrependLabel={true}
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default GiaSCETransferidoEditView;
