import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { CardBody, Col, Row } from 'reactstrap';
import {
  Accordion,
  AccordionItem,
  ColumnsProps,
  DetailManagerForm,
  DetailManagerFormProps,
  EditForm,
  EditViewProps,
  getSelectedItem,
  InputGroupInline,
  InputType,
  Tab,
  TabGroup,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { CLASS_NAME, Gia } from '../../../../../models/dtos/mlf/gia';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import GiaALCZFMEditView from '../GiaALCZFMEditView';
import GiaOPDeducaoEditView from '../giaOP/GiaOPDeducaoEditView';
import GiaOPEstornoCreditoEditView from '../giaOP/GiaOPEstornoCreditoEditView';
import GiaOPEstornoDebitoEditView from '../giaOP/GiaOPEstornoDebitoEditView';
import GiaOPOutroCreditoEditView from '../giaOP/GiaOPOutroCreditoEditView';
import GiaOPPrazoEspecialEditView from '../giaOP/GiaOPPrazoEspecialEditView';
import GiaOutroIcmsDevidoEditView from '../GiaOutroIcmsDevidoEditView';
import GiaSCECompensadoEstabelecimentoEditView from '../giaSCE/GiaSCECompensadoEstabelecimentoEditView';
import GiaSCECompensadoProprioEditView from '../giaSCE/GiaSCECompensadoProprioEditView';
import GiaSCEReciboEditView from '../giaSCE/GiaSCEReciboEditView';
import GiaSCETransferidoEditView from '../giaSCE/GiaSCETransferidoEditView';
import GiaSTDeducaoEditView from '../giaST/GiaSTDeducaoEditView';
import GiaSTEstornoCreditoEditView from '../giaST/GiaSTEstornoCreditoEditView';
import GiaSTEstornoDebitoEditView from '../giaST/GiaSTEstornoDebitoEditView';
import GiaSTOutroCreditoEditView from '../giaST/GiaSTOutroCreditoEditView';
import GiaSTPrazoEspecialEditView from '../giaST/GiaSTPrazoEspecialEditView';

interface GiaEditViewProps extends EditViewProps<Gia> {}

const GiaEditView: FC<GiaEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<Gia>({
    defaultValues: {
      empresa: globalReducer.globalParameter.empresa,
      ...selectedItem,
    },
    mode: 'onBlur',
  });

  // Colunas para todas as telas das abas ST, OP e Outro Icms
  const colunasGiaOPSTIcms: ColumnsProps[] = [
    {
      dataField: 'ocorrencia.codigo',
      text: 'Ocorrência',
    },
    {
      dataField: 'valor',
      text: 'Valor',
    },
  ];

  // Configurações para aba de OP -> Crédito -> Outro Crédito

  const giaOPOutroCreditoFormProps: DetailManagerFormProps = {
    columns: colunasGiaOPSTIcms,
    detailView: GiaOPOutroCreditoEditView,
    formProps,
    propName: 'giaOPOutroCredito',
    title: 'GIA - Outro Crédito',
  };

  // Configurações para aba de OP -> Crédito -> Estorno de Débito

  const giaOPEstornoDebitoFormProps: DetailManagerFormProps = {
    columns: colunasGiaOPSTIcms,
    detailView: GiaOPEstornoDebitoEditView,
    formProps,
    propName: 'giaOPEstornoDebito',
    title: 'GIA - Estorno de Débito',
  };

  // Configurações para aba de OP -> Crédito -> Deduções

  const giaOPDeducaoFormProps: DetailManagerFormProps = {
    columns: colunasGiaOPSTIcms,
    detailView: GiaOPDeducaoEditView,
    formProps,
    propName: 'giaOPDeducao',
    title: 'GIA - Deduções',
  };

  // Configurações para aba de OP -> Crédito -> Prazos Especiais

  const giaOPPrazoEspecialFormProps: DetailManagerFormProps = {
    columns: colunasGiaOPSTIcms,
    detailView: GiaOPPrazoEspecialEditView,
    formProps,
    propName: 'giaOPPrazoEspecial',
    title: 'GIA - Prazos Especiais',
  };

  // Configurações para aba de OP -> Débito -> Outro Débito

  const giaOPOutroDebitoFormProps: DetailManagerFormProps = {
    columns: colunasGiaOPSTIcms,
    detailView: GiaOPPrazoEspecialEditView,
    formProps,
    propName: 'giaOPOutroDebito',
    title: 'GIA - Outro Débito',
  };

  // Configurações para aba de OP -> Débito -> Estorno de Crédito

  const giaOPEstornoCreditoFormProps: DetailManagerFormProps = {
    columns: colunasGiaOPSTIcms,
    detailView: GiaOPEstornoCreditoEditView,
    formProps,
    propName: 'giaOPEstornoCredito',
    title: 'GIA - Estorno de Crédito',
  };

  // Configurações para aba de ST -> Crédito -> Outro Crédito

  const giaSTOutroCreditoFormProps: DetailManagerFormProps = {
    columns: colunasGiaOPSTIcms,
    detailView: GiaSTOutroCreditoEditView,
    formProps,
    propName: 'giaSTOutroCredito',
    title: 'GIA - Outro Crédito',
  };

  // Configurações para aba de ST -> Crédito -> Estorno de Débito

  const giaSTEstornoDebitoFormProps: DetailManagerFormProps = {
    columns: colunasGiaOPSTIcms,
    detailView: GiaSTEstornoDebitoEditView,
    formProps,
    propName: 'giaSTEstornoDebito',
    title: 'GIA - Estorno de Débito',
  };

  // Configurações para aba de ST -> Crédito -> Deduções

  const giaSTDeducaoFormProps: DetailManagerFormProps = {
    columns: colunasGiaOPSTIcms,
    detailView: GiaSTDeducaoEditView,
    formProps,
    propName: 'giaSTDeducao',
    title: 'GIA - Deduções',
  };

  // Configurações para aba de ST -> Crédito -> Prazos Especiais

  const giaSTPrazoEspecialFormProps: DetailManagerFormProps = {
    columns: colunasGiaOPSTIcms,
    detailView: GiaSTPrazoEspecialEditView,
    formProps,
    propName: 'giaSTPrazoEspecial',
    title: 'GIA - Prazos Especiais',
  };

  // Configurações para aba de ST -> Débito -> Outro Débito

  const giaSTOutroDebitoFormProps: DetailManagerFormProps = {
    columns: colunasGiaOPSTIcms,
    detailView: GiaSTPrazoEspecialEditView,
    formProps,
    propName: 'giaSTOutroDebito',
    title: 'GIA - Outro Débito',
  };

  // Configurações para aba de ST -> Débito -> Estorno de Crédito

  const giaSTEstornoCreditoFormProps: DetailManagerFormProps = {
    columns: colunasGiaOPSTIcms,
    detailView: GiaSTEstornoCreditoEditView,
    formProps,
    propName: 'giaSTEstornoCredito',
    title: 'GIA - Estorno de Crédito',
  };

  // Configurações para aba de Outros ICMS Devidos

  const giaOutroIcmsDevidoFormProps: DetailManagerFormProps = {
    columns: colunasGiaOPSTIcms,
    detailView: GiaOutroIcmsDevidoEditView,
    formProps,
    propName: 'giaOutroIcmsDevido',
    title: 'GIA - Outro ICMS Devido',
  };

  // Configurações para aba de Zona Franca, ALC

  const colunasALCZFM: ColumnsProps[] = [
    {
      dataField: 'saida.numero',
      text: 'Saída',
    },
    {
      dataField: 'uf.sigla',
      text: 'UF',
    },
    {
      dataField: 'municipio.nome',
      text: 'Cidade',
    },
    {
      dataField: 'vrIcmsAbatido',
      text: 'Vr. ICMS Abatido',
    },
  ];

  const giaALCZFMFormProps: DetailManagerFormProps = {
    columns: colunasALCZFM,
    detailView: GiaALCZFMEditView,
    formProps,
    propName: 'giaALCZFM',
    title: 'GIA - Zona Franca e ALC',
  };

  // Configurações para aba de Compensado Entre Estabelecimentos

  const colunasGiaSCECompEstab: ColumnsProps[] = [
    {
      dataField: 'finalidade.codigo',
      text: 'Finalidade',
    },
    {
      dataField: 'saida.numero',
      text: 'Saída',
    },
    {
      dataField: 'valor',
      text: 'Valor',
    },
  ];

  const giaSCECompEstabFormProps: DetailManagerFormProps = {
    columns: colunasGiaSCECompEstab,
    detailView: GiaSCECompensadoEstabelecimentoEditView,
    formProps,
    propName: 'giaSCECompensadoEstabelecimento',
    title: 'GIA - Compensado Entre Estabelecimentos',
  };

  // Configurações para aba de Compensado Próprio

  const colunasGiaSCECompensadoProprio: ColumnsProps[] = [
    {
      dataField: 'finalidade.codigo',
      text: 'Finalidade',
    },
    {
      dataField: 'valor',
      text: 'Valor',
    },
  ];

  const giaSCECompensadoProprioFormProps: DetailManagerFormProps = {
    columns: colunasGiaSCECompensadoProprio,
    detailView: GiaSCECompensadoProprioEditView,
    formProps,
    propName: 'giaSCECompensadoProprio',
    title: 'GIA - Compensado Próprio',
  };

  // Configurações para aba de Recebidos

  const colunasGiaSCERecebido: ColumnsProps[] = [
    {
      dataField: 'finalidade.codigo',
      text: 'Finalidade',
    },
    {
      dataField: 'entrada.numero',
      text: 'Entrada',
    },
    {
      dataField: 'valor',
      text: 'Valor',
    },
  ];

  const giaSCERecebidoFormProps: DetailManagerFormProps = {
    columns: colunasGiaSCERecebido,
    detailView: GiaSCEReciboEditView,
    formProps,
    propName: 'giaSCERecebido',
    title: 'GIA - Recebidos',
  };

  // Configurações para aba de Transferidos

  const colunasGiaSCETransferido: ColumnsProps[] = [
    {
      dataField: 'finalidade.codigo',
      text: 'Finalidade',
    },
    {
      dataField: 'saida.numero',
      text: 'Saída',
    },
    {
      dataField: 'valor',
      text: 'Valor',
    },
  ];

  const giaSCETransferidoFormProps: DetailManagerFormProps = {
    columns: colunasGiaSCETransferido,
    detailView: GiaSCETransferidoEditView,
    formProps,
    propName: 'giaSCETransferido',
    title: 'GIA - Transferidos',
  };

  return (
    <WindowComponent title="GIA - Informações Complementares" dependencies={[() => hasEmpresa(globalReducer.globalParameter)]} wikiId={39}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline formProps={formProps} id="empresa.id" hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="referencia"
              isPrependLabel={true}
              label="Referência"
              type={InputType.REFERENCE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <TabGroup formProps={formProps}>
              <Tab title="Operações Próprias">
                <Accordion>
                  <AccordionItem title="Crédito">
                    <DetailManagerForm {...props} {...giaOPOutroCreditoFormProps} keyField="rowIndex" />
                    <DetailManagerForm {...props} {...giaOPEstornoDebitoFormProps} keyField="rowIndex" />
                    <DetailManagerForm {...props} {...giaOPDeducaoFormProps} keyField="rowIndex" />
                    <DetailManagerForm {...props} {...giaOPPrazoEspecialFormProps} keyField="rowIndex" />
                  </AccordionItem>
                  <AccordionItem title="Débito">
                    <DetailManagerForm {...props} {...giaOPOutroDebitoFormProps} keyField="rowIndex" />
                    <DetailManagerForm {...props} {...giaOPEstornoCreditoFormProps} keyField="rowIndex" />
                  </AccordionItem>
                </Accordion>
              </Tab>
              <Tab title="Substituição Tributária">
                <Accordion>
                  <AccordionItem title="Crédito">
                    <DetailManagerForm {...props} {...giaSTOutroCreditoFormProps} keyField="rowIndex" />
                    <DetailManagerForm {...props} {...giaSTEstornoDebitoFormProps} keyField="rowIndex" />
                    <DetailManagerForm {...props} {...giaSTDeducaoFormProps} keyField="rowIndex" />
                    <DetailManagerForm {...props} {...giaSTPrazoEspecialFormProps} keyField="rowIndex" />
                  </AccordionItem>
                  <AccordionItem title="Débito">
                    <DetailManagerForm {...props} {...giaSTOutroDebitoFormProps} keyField="rowIndex" />
                    <DetailManagerForm {...props} {...giaSTEstornoCreditoFormProps} keyField="rowIndex" />
                  </AccordionItem>
                </Accordion>
              </Tab>
              <Tab title="Outros ICMS Devidos">
                <Row>
                  <Col>
                    <DetailManagerForm {...props} {...giaOutroIcmsDevidoFormProps} keyField="rowIndex" />
                  </Col>
                </Row>
              </Tab>
              <Tab title="Zona Franca, ALC">
                <Row>
                  <Col>
                    <DetailManagerForm {...props} {...giaALCZFMFormProps} keyField="rowIndex" />
                  </Col>
                </Row>
              </Tab>
              <Tab title="Saldo Credor de Exportação">
                <CardBody>
                  <Row>
                    <Col>
                      <DetailManagerForm {...props} {...giaSCECompEstabFormProps} keyField="rowIndex" />
                    </Col>
                  </Row>
                </CardBody>
                <CardBody>
                  <Row>
                    <Col>
                      <DetailManagerForm {...props} {...giaSCECompensadoProprioFormProps} keyField="rowIndex" />
                    </Col>
                  </Row>
                </CardBody>
                <CardBody>
                  <Row>
                    <Col>
                      <DetailManagerForm {...props} {...giaSCERecebidoFormProps} keyField="rowIndex" />
                    </Col>
                  </Row>
                </CardBody>
                <CardBody>
                  <Row>
                    <Col>
                      <DetailManagerForm {...props} {...giaSCETransferidoFormProps} keyField="rowIndex" />
                    </Col>
                  </Row>
                </CardBody>
              </Tab>
            </TabGroup>
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default GiaEditView;
