import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType, onlyNumber } from 'summer';
import { GiaSCERecebido } from '../../../../../models/dtos/mlf/giaSCERecebido';
import FinalidadeIS from '../../../../components/ger/finalidadeIS/FinalidadeIS';

const GiaSCEReciboEditView: FC<DetailEditViewFormProps<GiaSCERecebido>> = props => {
  const formProps = useForm<GiaSCERecebido>({ defaultValues: { ...props.selectedItem }, mode: 'onBlur' });

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <FinalidadeIS
            formProps={{ ...formProps, validation: { required: true } }}
            label="Finalidade"
            placeholder="Selecione uma finalidade"
            id="finalidade"
            isPrependLabel={true}
            inputSearchProps={{ size: 'xl' }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          {
            // TODO implementar EntradaIS aqui
          }
          <p>Colocar Input para selecionar Entrada aqui -- required</p>
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="valor"
            isPrependLabel={true}
            label="Valor"
            placeholder="Informe o Valor"
            type={InputType.NUMBER}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 300 } }}
            id="dadoComplementar"
            isPrependLabel={true}
            label="Dado Complementar"
            placeholder="Informe o Dado Complementar"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 20 } }}
            label="Número do Processo"
            placeholder="Informe o Número do Processo"
            id="numeroProcesso"
            isPrependLabel={true}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { ...onlyNumber(0, 4) } }}
            label="Ano do processo"
            placeholder="Informe o Ano do Processo"
            id="anoProcesso"
            isPrependLabel={true}
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default GiaSCEReciboEditView;
