import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { GiaSCECompensadoEstabelecimento } from '../../../../../models/dtos/mlf/giaSCECompensadoEstabelecimento';
import FinalidadeIS from '../../../../components/ger/finalidadeIS/FinalidadeIS';

const GiaSCECompensadoEstabelecimentoEditView: FC<DetailEditViewFormProps<GiaSCECompensadoEstabelecimento>> = props => {
  const formProps = useForm<GiaSCECompensadoEstabelecimento>({ defaultValues: { ...props.selectedItem }, mode: 'onBlur' });

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <FinalidadeIS
            formProps={{ ...formProps, validation: { required: true } }}
            label="Finalidade"
            placeholder="Selecione uma finalidade"
            id="finalidade"
            isPrependLabel={true}
            inputSearchProps={{ size: 'xl' }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          {
            // TODO implementar SaidaIS aqui
          }
          <p>Input para selecionar Saida -- à ser implementado</p>
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="valor"
            isPrependLabel={true}
            label="Valor"
            placeholder="Informe o Valor"
            type={InputType.NUMBER}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 300 } }}
            id="dadoComplementar"
            isPrependLabel={true}
            label="Dado Complementar"
            placeholder="Informe o Dado Complementar"
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default GiaSCECompensadoEstabelecimentoEditView;
