import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { GiaALCZFM } from '../../../../models/dtos/mlf/giaALCZFM';
import CepLocalidadeIS from '../../../components/ger/cepLocalidadeIS/CepLocalidadeIS';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';

const GiaALCZFMEditView: FC<DetailEditViewFormProps<GiaALCZFM>> = props => {
  const formProps = useForm<GiaALCZFM>({ defaultValues: { ...props.selectedItem }, mode: 'onBlur' });

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          {
            // TODO implementar SaidaIS aqui
          }
          <p>Colocar Input para selecionar Saída aqui -- required</p>
        </Col>
      </Row>
      <Row>
        <Col>
          <CepUFIS formProps={{ ...formProps, validation: { required: true } }} id="uf" />
        </Col>
      </Row>
      <Row>
        <Col>
          <CepLocalidadeIS formProps={{ ...formProps, validation: { required: true } }} id="municipio" />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={formProps}
            label="Vr. ICMS Abatido"
            placeholder="Informe o Valor ICMS Abatido"
            id="vrIcmsAbatido"
            isPrependLabel={true}
            type={InputType.NUMBER}
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default GiaALCZFMEditView;
